.services {
  height: 100%;
}

.main_services {
  list-style-type: none;
  font-size: medium;
  padding-top: 5px;
  line-height: 2rem;
  width: 80vw;
  margin-left: auto;
  margin-right: auto;
}

.services__item {
  margin: 5px;
}

.services--title {
  font-size: large;
  color: var(--cyan);
  margin: 5px;
  padding: 5px;
  padding-left: 0;
  margin-left: 0px;
}

.servicespage__listitem {
  list-style: none;
}

.servicespage__listitem::before {
  content: ">";
  /* Add content: \2022 is the CSS Code/unicode for a bullet */
  color: var(--cyan);
  /* Change the color */
  font-weight: bold;
  /* If you want it to be bold */
  display: inline-block;
  /* Needed to add space between the bullet and the text */
  width: 1em;
  /* Also needed for space (tweak if needed) */
  margin-left: -1em;
}
.caption__services {
  font-size: small;
  opacity: 0.9;
}
.staffpicture__services {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 25px;
}
